import { observer } from "mobx-react-lite";
import { useRef } from "react";
import { Button, CreateProjectForm } from "@/components";
import { ProjectTile } from "@/components/ProjectTile";

import store from "@/MST/store";

import { UserProjectType } from "@/types/common";

import styles from "./ProjectsList.module.scss";

const ProjectsList: React.FC = () => {
  
  const createProjectModalRef = useRef<HTMLDialogElement>(null);
  const { userProjects, createProject, deleteProject } = store.user;

  const onCreateProjectClick = () => {
    createProjectModalRef.current!.showModal();
  };

  const onCloseClick = () => {
    createProjectModalRef.current!.close();
  };

  const onCreateClick = async (projectData: Partial<UserProjectType>) => {
    await createProject(projectData);
  };

  const onDeleteClick = async (projectId: string) => {
    await deleteProject(projectId);
  };

  return (
    <div className={styles.projectsContainer}>
      <Button
        icon="icon_plus"
        text={"Add project"}
        onClick={onCreateProjectClick}
      />
      <div className={styles.projectTilesWrapper}>
        {userProjects.length
          ? userProjects.map((project) => <ProjectTile key={project._id} {...project} onDelete={onDeleteClick} />)
          : "You dont have any projects yet."}
      </div>

      <dialog ref={createProjectModalRef}>
        <CreateProjectForm
          onCreateClick={onCreateClick}
          onCloseClick={onCloseClick}
        />
      </dialog>
    </div>
  );
};

export default observer(ProjectsList);
